import { Box, Grid, HStack, Image, Link, List, ListItem, Stack, StackDivider, StackItem, Text } from "@chakra-ui/react"
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, EffectFade, Navigation } from "swiper";
import 'swiper/css';

export const ProjectCard = ({ img, title, desc, ts, gLink, dLink }) => {
    return (
        <Stack gap={"20px"} className="project-card">
            <img src="" alt="" />
            <Box margin={"auto"} width={{ base: "200px", sm: "300px", md: "400px", lg: "500px" }}>
                <Swiper
                    modules={[Autoplay, EffectFade, Navigation]}
                    style={{ width: "100%" }}
                    autoplay={{ delay: 2000, pauseOnMouseEnter: true, disableOnInteraction: false }}
                    navigation
                    loop
                >
                    {img.map(e => {
                        return <SwiperSlide key={Math.random()}><Image src={e} alt={e} /></SwiperSlide>
                    })}
                </Swiper>
            </Box>
            <Text fontWeight={"medium"} fontSize={"3xl"} className="project-title">{title}</Text>
            <Text className="project-description">{desc}</Text>
            <HStack justify={"center"} divider={<StackDivider borderColor={"black"} />} className="project-tech-stack">{ts.map(e => {
                return <StackItem key={Math.random()}>{e}</StackItem>
            })}</HStack>
            <Grid templateColumns={"1fr 1fr"}>
                <Link target="_blank" href={gLink} className="project-github-link" >Github</Link>
                <Link target="_blank" href={dLink} className="project-deployed-link" >Deployed</Link>
            </Grid>
        </Stack >
    )
}