import { FormControl, FormLabel, Grid, GridItem, Input, Text, Textarea } from "@chakra-ui/react";
import emailjs from '@emailjs/browser';
import { useRef } from "react";

export const Contact = () => {
    const form = useRef();

    const sendEmail = (e) => {
        e.preventDefault();
        console.log(form.current)

        emailjs.sendForm("service_lpwm39q", 'template_5wlphl5', form.current, 'OLY_XIwjYqHr7UZxX')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
    };
    return (
        <Grid p={"150px 0"} templateColumns={{ base: "1fr", sm: "1fr 1fr", md: "1fr 1fr", lg: "1fr 1.5fr" }} width={{ base: "100%", md: "90%", lg: "85%" }} margin={"auto"} className="Contact" id="contact">
            <GridItem padding={"20px 5%"}>
                <Text id="contact-github"><a rel="noreferrer" href="https://github.com/Himanshu-t-y-a-g-i" target="_blank">My Github profile</a></Text>
                <Text id="contact-linkedin"><a rel="noreferrer" href="https://www.linkedin.com/in/himanshutyagi127001/" target="_blank">LinkedIn</a></Text>
                <Text id="contact-phone">Contact no.: 7877564728</Text>
                <Text id="contact-email">Email Address: himanshutyagi0158@gmail.com</Text>
            </GridItem>
            <GridItem padding={"20px 5%"} borderRadius={"20px"} boxShadow={"0px 0px 10px 2px rgb(210, 210, 210)"}>
                <Text align={"center"} fontSize={"2xl"} mb={"30px"}>Contact Me</Text>
                <form ref={form} onSubmit={sendEmail}>
                    <FormLabel>Name</FormLabel>
                    <Input mb={"20px"} type="text" name="user_name" />
                    <FormLabel>Email</FormLabel>
                    <Input mb={"20px"} type="email" name="user_email" />
                    <FormLabel>Message</FormLabel>
                    <Textarea mb={"20px"} name="message" />
                    <Input _hover={{ cursor: "pointer" }} mb={"20px"} type="submit" value="Send" />
                </form>
            </GridItem>
        </Grid>
    )
}